@import url(https://fonts.googleapis.com/css?family=Raleway|Rubik&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#nav {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  width: 100%;
  overflow: hidden;
  z-index: 9999;
  background-color: whitesmoke;
  box-sizing: border-box;
  padding: 20px 15px 15px 15px;
  border-bottom: 3px solid black;
}

#nav::after {
  content: "";
  display: block;
  clear: both;
}

#name {
  float: left;
  margin: 0px 20px 0px 10px;
  font-size: 1.3em;
}

#navList {
  list-style-type: none;
}

#navList > li {
  line-height: 1.3em;
  float: left;
}

#navList > li > a {
  color: black;
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 0.15em;
  display: inline-block;
  padding: 0 10px 5px;
  position: relative;
}

#navList > li > a::after {
  background: none repeat scroll 0 0 transparent;
  bottom: 0;
  content: "";
  display: block;
  height: 2px;
  left: 50%;
  position: absolute;
  background: black;
  -webkit-transition: width 0.3s ease 0s, left 0.3s ease 0s;
  transition: width 0.3s ease 0s, left 0.3s ease 0s;
  width: 0;
}

#navList > li > a:hover::after {
  width: 100%;
  left: 0;
}

#navList > li > a:visited {
  color: black;
}

#home {
  text-align: center;
  border-bottom: 2px solid black;
  padding: 60px 0;
}

#home::after {
  content: "";
  display: block;
  clear: both;
}

#intro {
  float: left;
  width: 50%;
  margin: 50px;
  line-height: 1.5em;
  box-sizing: border-box;
}

#intro > h1 {
  margin: 40px 0;
  font-size: 2.5em;
}

#intro > h2 {
  font-size: 1.1em;
}

#links {
  margin: 50px;
  float: left;
}

#linkList {
  display: flex;
  flex-wrap: wrap;
}

#linkList > li{
  padding: 20px;
  text-align: center;
  display: inline;
}

.column {
  float: left;
  flex: 50% 1;
  max-width: 50%;
}

.icon {
  width: 50px;
  padding: 20px;
}


#about {
  padding: 30px;
  border-bottom: 2px solid black;
}

#about::after {
  content: "";
  display: block;
  clear: both;
}

#languageIcons > h1 {
  margin: 30px 40px;
  font-size: 2em;
  font-weight: 600;
}

#languageList {
  margin: 0 40px;
}


.row > li {
  display: inline;
}

.lanIcon {
  margin: 15px;
  width: 60px;
  height: 60px;
}

#aboutMe {
  float: right;
  width: 60%;
}

#aboutMe > h2 {
  margin: 0 30px; 
  font-size: 1.8em;
  font-weight: 600;
}

#aboutMe > p {
  margin: 30px;
  line-height: 1.3em;
  font-size: 1.3em;
}
#project {
  padding: 30px;
  border-bottom: 2px solid lightgray;
}

#project::after {
  content: "";
  display: block;
  clear: both;
}

#project  > h1 {
  font-size: 2.5em;
  font-weight: 600;
  margin: 30px;
}


#towerDefense, #dailyPlanner, #toDoApp {
  margin: 20px;
  padding-bottom: 20px;
  border-bottom: 2px solid lightgray;
}

#towerDefense::after, #dailyPlanner::after, #toDoApp::after {
  content: "";
  display: block;
  clear: both;
}

#projectLeft1, #projectLeft2, #projectLeft3 {
  float: left;
}

.projectIcon {
  width: 180px;
  margin: 20px;
  margin-right: 40px;
}

.madeWith {
  margin: 10px;
  font-weight: 600;
}

.exp {
  margin: 10px;
  line-height: 1.3em;
}

#towerDefense > h1, #dailyPlanner > h1, #toDoApp > h1{
  padding: 15px;
  font-size: 2em;
  font-weight: 700;
}

.projectLanguage {
  margin: 10px;
  width: 50px;
  height: 50px;
}

#viewCode {
  margin: 40px;
  text-align: center;
  font-size: 1.5em;
}
#education {
  padding: 30px;
  border-bottom: 2px solid lightgray;
}

#education::after {
  content: "";
  display: block;
  clear: both;
}

#waterlooIcon {
  margin: 10px;
  width: 100px;
}

#waterlooIconDiv {
  float: left;
}

#educationInfo {
  margin: 20px;
  float: left;
}

#class {
  font-size: 1.3em;
  font-weight: 600;
}

#candidate {
  line-height: 1.3em;
}
#footer {
  margin: 40px;
  margin-bottom: 60px;
  text-align: center;
}

#contact {
  line-height: 1.3em;
}

#contact > h1 {
  margin: 20px;
  font-size: 2em;
  font-weight: 700;
}

#contact > h2 {
  font-size: 1.5em;
}
html {
  scroll-behavior: smooth;
}

#main {
  font-family: 'Raleway', 'Rubik', sans-serif;
}

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
